import PropTypes from "prop-types";
import {Link} from "react-router-dom";

import style from "./css/Movie.module.css";

function Movie( {id, title, genres, summary, posterImg,
    rating, year, runtime} ){
    return (
        <div className={style.movieLayer}>
            <h2>
                <Link to={`/movie/${id}`}>{title}</Link>
            </h2>
            <div className={style.leftArea}>
                <div className={style.posterBox}>
                    <img src={posterImg} alt={title}/>
                </div>                
            </div>
            <div className={style.rightArea}>
                <p>개봉일 : {year}년 </p>
                <p>상영시간 : {Math.floor(runtime/60)}시간 {runtime%60}분 </p>
                <p>장르 : {genres.map((g, idx) => (
                            <span key={idx}>{g}</span>
                            ))}
                </p>
                <p className={style.rating}>평점 : {rating} / 10 </p>
                <div className={style.summary}>
                    {summary.length > 200 ? 
                        summary.slice(0, 200) + " ..." : 
                        summary
                    }
                </div>
            </div>
        </div>
    );
}

Movie.prototype = {
    id: PropTypes.number.isRequired,
    title: PropTypes.string.isRequired,
    genres: PropTypes.arrayOf(PropTypes.string).isRequired,
    summary: PropTypes.string.isRequired,
    posterImg: PropTypes.string.isRequired
}

export default Movie;